/* PROFILE PICTURE SECTION */
.profilePicture {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 4rem;
  margin-bottom: 1rem;
}

/* Picture container and styling */
.profilePicture .pictureContainer {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 100px;
  overflow: hidden; /* Ensure the image fits within the border */
}

.profilePicture .pictureContainer .imageBorder {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  border-radius: 100px;
  border: 5px solid var(--color-profile-picture-border);
  box-shadow: 0 0 0 1px var(--color-profile-picture-shadow);
  transition: transform 0.3s ease; /* Smooth transition on hover */
}

.profilePicture .pictureContainer .imageBorder:hover {
  transform: scale(1.05); /* Slight zoom effect on hover */
}

/* Edit button styling */
.pictureContainer .editButton {
  background-color: var(--color-profile-picture-edit-button-bg);
  color: var(--color-profile-picture-edit-button);

  position: absolute;
  bottom: 10px;
  right: 0;

  font-size: 1rem;
  width: 38px;
  height: 38px;

  border-radius: 100px;
  border: 2px solid var(--color-profile-picture-border);
  cursor: pointer;
  transition: background-color 0.3s ease; /* Transition on hover */
}

.pictureContainer .editButton:hover {
  background-color: var(--color-profile-picture-edit-button-bg-hover); /* Change on hover */
}

/* Edit picture mode (input fields) */
.pictureContainer .editPictureMode {
  width: 100%;
}

.pictureContainer .editPictureMode p {
  color: var(--color-profile-picture-edit-text);
  font-size: 1rem;
  margin-bottom: 0.3rem;
  font-weight: 500;
  font-family: 'Poppins', sans-serif; /* Friendly and approachable font */
}

.pictureContainer .editPictureMode input {
  color: #000;
  background-color: rgb(245, 246, 247);

  font-size: 0.875rem;
  width: 100%;
  padding: 0.4rem 0.3rem;
  margin-top: 0.3rem;

  border-radius: var(--border-radius-input);
  border: 1px solid rgb(238, 238, 238);
  border-bottom: 2px solid rgb(228, 228, 228);
  font-family: 'Roboto', sans-serif; /* Playful and modern font for inputs */
}

.pictureContainer .editPictureMode input:focus {
  outline: none;
  border-color: var(--color-primary); /* Highlight on focus */
}

/* Save button in edit mode */
.pictureContainer .editPictureMode button {
  background-color: var(--color-primary);
  color: var(--white);

  font-size: 1.25rem;
  width: 100%;
  padding: 0.1rem 1rem;
  margin-top: 0.5rem;

  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pictureContainer .editPictureMode button:hover {
  background-color: var(--color-primary-dark); /* Darker shade on hover */
}
